/* modules */

.sidebar {
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  margin: 0 auto;
  max-width: 357px;
  padding: 23px 25px 25px 25px;
  background: $color-lightgray;
  font-family: $font-roboto-light;
  font-size: rem(13);
  &__title {
    color: $color-orange;
    text-transform: uppercase;
    font-size: rem(18);
    font-family: $font-roboto-medium;
    line-height: 1;
    margin-bottom: 15px; }
  &__navigation {
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: none;
        margin: 0 0 6px 0;
        &:last-child {
          margin-bottom: 0; }
        &>a {
          display: block;
          position: relative;
          text-decoration: none;
          color: $color-dark;
          padding: 0 35px 0 15px;
          &:hover {
            color: $color-orange;
            &::before {
              color: $color-orange; } }
          &::before {
            content: "\f363";
            font-family: $font-ionicons;
            position: absolute;
            left: 1px;
            top: 0px;
            font-size: rem(18);
            color: #d1d4da;
            line-height: 1; }
          span {
            position: absolute;
            right: 0;
            top: 3px;
            color: $color-gray;
            display: block;
            line-height: 1; } }
        &.active {
          &>a {
            color: $color-orange;
            &::before {
              color: $color-orange; } }
          font-family: $font-roboto-medium; } } }
    &--services {
      margin-bottom: 25px;
      &:last-child {
        margin-bottom: 0; }
      ul {
        li {
          &>a {
            padding-right: 0;
            span {
              display: none; } } } } } } }

.breadcrumbs {
  background: #e2a06c url('../img/breadcrumbs-background.png') top center no-repeat;
  background-size: cover;
  color: $color-white;
  font-size: rem(10);
  min-height: 120px;
  padding-top: 33px;
  padding-bottom: 25px;
  ul {
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      margin: 0;
      color: #fcccb8;
      display: inline-block;
      &+li {
        &::before {
          content: '/';
          display: inline-block;
          margin: 0 5px 0 3px; } }
      a {
        color: #fcccb8; }
      &:last-child {
        display: block;
        margin-left: 0;
        &::before {
          content: '';
          display: none; } } } }
  .h1, h1 {
    margin-bottom: 0;
    color: $color-white;
    line-height: 1.1; } }

.news {
  &__block {
    margin-bottom: 45px;
    &-img {
      position: relative;
      overflow: hidden;
      border-radius: 4px;
      margin-bottom: 35px;
      a {
        position: absolute;
        z-index: 5;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0; }
      &:hover {
        .news__block-btn {
          background: #c04631; } } }
    &-btn {
      position: absolute;
      right: 23px;
      bottom: 21px;
      height: 30px;
      line-height: 30px;
      width: 163px;
      border-radius: 30px;
      background: $color-orange;
      color: $color-white;
      font-size: rem(11);
      text-transform: uppercase;
      font-family: $font-roboto-bold;
      text-align: center;
      box-shadow: 0 0 15px rgba($color-orange,.25);
      transition: background 0.35s; }
    &-title {
      font-size: rem(20);
      font-family: $font-roboto-medium;
      line-height: 1.2;
      margin-bottom: 25px; }
    p {
      &:last-child {
        margin-bottom: 0; } } } }

.pagination {
  text-align: center;
  font-size: rem(14);
  ul {
    display: inline-block;
    margin: 0;
    padding: 0;
    @include clearfix;
    li {
      float: left;
      margin: 0;
      list-style: none;
      &>a,&>span {
        display: block;
        width: 30px;
        line-height: 30px;
        height: 30px;
        text-align: center;
        border-radius: 50%;
        color: $primary-color;
        background: #f7f8fa; }
      &>a {
        &:hover {
          text-decoration: none;
          background: $color-orange;
          color: $color-white; } }
      &+li {
        margin-left: 7px; }
      &.active {
        &>a,&>span {
          background: $color-orange;
          color: $color-white; } } } } }

.about {
  &__img {
    float: right;
    margin-left: 30px;
    margin-bottom: 30px; } }

.advantages {
  &__table {
    margin-bottom: 25px;
    td {
      vertical-align: top;
      &+td {
        padding-left: 30px; }
      &:first-child {
        width: 1px; } } } }

ul.list {
  padding: 0;
  margin: 0 0 25px 0;
  li {
    list-style: none;
    position: relative;
    padding-left: 23px;
    min-height: 15px;
    margin: 0 0 5px 0;
    line-height: 1.2;
    &::before {
      content: '';
      position: absolute;
      left: 1px;
      top: 3px;
      width: 0;
      width: 12px;
      height: 13px;
      background: url('data:image/png;base64,') 0 0 no-repeat; } } }

.reviews {
  &__row {
    font-size: rem(13);
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid #e3e3e3;
    &:last-child {
      border-bottom: 0; } }
  &__img {
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    max-width: 100%;
    height: auto;
    display: block; }
  &__title {
    font-family: $font-roboto-medium;
    font-size: rem(20);
    line-height: 1.2;
    margin-bottom: 15px; }
  &__content {
    // p
    //   &:last-child
 }    //     margin-bottom: 0
  &__name {
    font-family: $font-roboto-medium;
    font-size: rem(13);
    span {
      font-family: $font-roboto-light;
      color: #a0a0a0; } } }

.contact {
  &__map {
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    margin-bottom: 40px;
    height: 0;
    padding-bottom: 400px;
    background: $color-lightgray; }
  &__content {
    margin-bottom: 50px;
    .table {
      td {
        vertical-align: top;
        &:first-child {
          text-align: center; } }
      &:last-child {
        margin-bottom: 0; } }
    a {
      &[href*='callto:'] {
        color: $primary-color;
        &:hover {
          color: $color-orange; } } } }
  &__form {
    background: $color-lightgray;
    overflow: hidden;
    border-radius: 4px;
    padding: 25px 34px;
    font-size: rem(12);
    min-height: 190px;
    .f20 {
      font-size: rem(20);
      font-family: $font-roboto-medium; }
    &-wrapper {
      display: table;
      width: 100%;
      &>div {
        display: table-cell;
        vertical-align: top;
        &:first-child {
          width: 45%; }
        &+div {
          padding-left: 30px; } }
      p {
        &:last-child {
          margin-bottom: 0; } } } } }

.specialists {
  position: relative;
  overflow: hidden;
  padding-top: 48px;
  margin-top: -48px;
  margin-right: 1px;
  &-wrapper {
    width: calc(100% + 1px); }
  &__block {
    width: 100%;
    max-width: 286px;
    font-size: rem(12);
    color: $primary-color;
    text-align: center;
    height: 320px;
    padding-top: 20px;
    border-right: 1px solid #eeeff2;
    margin: 0 auto; }
  &__img {
    height: 160px;
    width: 150px;
    position: relative;
    overflow: hidden;
    border-radius: 0 0 75px 75px;
    margin: 0 auto 20px;
    background: $color-lightgray;
    &::before, &::after {
      position: absolute;
      width: 44px;
      height: 44px;
      content: '';
      background: $color-white;
      top: -22px;
      border-radius: 50%; }
    &::before {
      left: -22px; }
    &::after {
      right: -22px; } }
  &__name {
    font-family: $font-roboto-medium;
    text-transform: uppercase;
    font-size: rem(16);
    margin-bottom: 20px;
    span {
      text-transform: none;
      font-size: rem(13);
      font-family: $font-roboto;
      display: block; } }
  &__content {
    .table {
      max-width: 140px;
      display: inline-table;
      text-align: left;
      td {
        padding-top: 0;
        padding-bottom: 0; }
      i {
        font-size: rem(14); } } }
  .owl-nav {
    position: absolute;
    top: -50px;
    right: 0; }
  &__nav {
    position: absolute;
    right: 0;
    top: 0; } }

#specialistsLeft, #specialistsRight, #newslistLeft, #newslistRight {
  cursor: pointer;
  display: inline-block;
  width: 30px;
  line-height: 30px;
  height: 30px;
  text-align: center;
  border-radius: 50%;
  color: $primary-color;
  background: #f7f8fa;
  &:hover, &:focus {
    color: $color-white;
    background: $color-orange; } }

.services {
  padding: 90px 0;
  background: $color-orange url('../img/services-background.png') top center no-repeat;
  background-size: cover;
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-right: -10px;
    @include clearfix; }
  &__block {
    background: $color-white;
    width: 285px;
    height: 80px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $font-roboto-bold;
    font-size: rem(14);
    text-transform: uppercase;
    color: $primary-color;
    text-align: center;
    position: relative;
    clip-path: polygon(0 0, 0 100%, 100% 100%, 101% 16px, calc(100% - 14px) 0);
    margin-bottom: 1px;
    margin-left: 1px;
    &::before {
      position: absolute;
      right: 0;
      top: 0;
      width: 14px;
      height: 14px;
      content: '';
      background: #d8d8d8; }
    &:hover, &:focus {
      text-decoration: none; } } }

.main-about {
  padding: 0;
  background: $color-lightgray;
  font-size: rem(15);
  color: $primary-color;
  position: relative;
  z-index: 15;
  &__wrapper {
    position: relative;
    padding: 80px 390px 70px 0;
    min-height: 500px;
    p {
      position: relative;
      z-index: 1; } }
  &__img {
    position: absolute;
    bottom: -25px;
    right: -116px; } }

.news-list {
  &__wrapper {
    margin: 0 -15px; }
  &__nav {
    margin-top: -55px;
    padding-left: 15px;
    position: absolute; }
  &__head {
    text-transform: uppercase;
    padding-left: 100px;
    font-size: rem(14);
    font-family: $font-roboto-medium;
    float: left;
    margin-bottom: 30px; }
  &__link {
    float: right;
    color: $color-orange;
    text-transform: uppercase;
    font-family: $font-roboto-medium;
    font-size: rem(14); }
  &__block {
    position: relative;
    background: $color-white;
    border-radius: 10px;
    overflow: hidden;
    height: 300px;
    width: 100%;
    border: 2px solid $color-lightgray;
    opacity: 0;
    transition: opacity 0.35s;
    &-img {
      position: absolute;
      left: 0;
      top: 0;
      width: 200px;
      bottom: 0;
      background: $color-gray;
      overflow: hidden;
      border-radius: 10px 0 0 10px;
      img {
        max-width: inherit;
        width: auto !important; } }
    &-content {
      padding: 45px 24px 10px 30px;
      margin-left: 176px;
      position: relative;
      z-index: 5;
      background: linear-gradient(to left, $color-white calc(100% - 15px), rgba($color-white,.7));
      height: 100%; }
    &-name {
      font-size: rem(20);
      margin-bottom: 15px;
      line-height: 22px;
      display: inline-block;
      color: $primary-color;
      &:hover {
        color: $color-orange;
        text-decoration: none; } }
    p {
      line-height: 21px; } } }

.active {
  .news-list__block {
    opacity: 1; } }

.four-blocks {
  padding: 60px 0;
  margin-top: -163px;
  background: linear-gradient(to top, $color-white, rgba($color-white,.9));
  position: relative;
  z-index: 10;
  &__block {
    display: table;
    text-transform: uppercase;
    font-family: $font-roboto-bold;
    font-size: rem(14);
    &>div {
      display: table-cell;
      vertical-align: middle;
      &+div {
        padding-left: 30px; }
      &:first-child {
        width: 1px; } } } }

.main-slider {
  background: $color-gray;
  &__carousel {
    z-index: inherit; }
  &__block {
    height: 690px;
    background-position: top center;
    background-repeat: no-repeat;
    // background-size: cover
    &--1 {
      background-image: url('../img/main-slider-img-1.jpg'); }
    &--2 {
      background-image: url('../img/main-slider-img-2.jpg'); } }
  .owl-dots {
    position: absolute;
    z-index: 15;
    width: 100%;
    text-align: center;
    margin-top: -155px; }
  .owl-theme .owl-dots .owl-dot {
    span {
      width: 66px;
      height: 3px;
      background: #c3c5c8; }
    &.active {
      span {
        background: #ee560e; } } } }

.popup {
  max-width: 290px;
  background: $color-white url('data:image/png;base64,') top center;
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 10px;
  &__wrapper {
    padding: 42px 34px 38px 34px; }
  &__close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: rem(20);
    color: $color-orange;
    cursor: pointer;
    i {
      display: block;
      line-height: 1; } }
  &__title {
    text-align: center;
    line-height: 1.2;
    color: $color-orange;
    font-size: rem(20);
    font-family: $font-roboto-bold;
    margin-bottom: 20px;
    text-transform: uppercase; }
  .form-checkbox__label {
    margin-right: -10px; } }

.special {
  margin-bottom: 65px;
  &__block {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    border: 2px solid #f3f4f5;
    &-wrapper {
      display: table;
      width: 100%;
      &>div {
        display: table-cell;
        vertical-align: top;
        &:first-child {
          width: 1px; } } }
    &-img {
      overflow: hidden;
      border-radius: 10px 0 0 10px;
      width: 567px;
      img {
        width: auto !important;
        display: block; } }
    &-label {
      position: absolute;
      left: 15px;
      top: 15px;
      border-radius: 25px;
      padding: 0 20px;
      text-transform: uppercase;
      color: $primary-color;
      background: #ffcf11;
      font-size: rem(11);
      height: 30px;
      line-height: 31px;
      font-family: $font-roboto-bold; }
    &-content {
      padding: 0 15px 0 30px;
      &-row {
        font-size: rem(14);
        &--1 {
          padding-top: 20px;
          padding-bottom: 15px;
          border-bottom: 1px solid #eaeaea;
          height: 116px;
          strong {
            white-space: nowrap;
            overflow: hidden;
            display: block;
            text-overflow: ellipsis;
            font-size: rem(20);
            line-height: 1.2;
            margin-bottom: 10px; }
          p {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis; } }
        &--2 {
          padding-top: 20px;
          height: 92px;
          p {
            .fa-rouble {
              font-size: rem(12);
              vertical-align: 0px; } } }
        &--3 {
          border-top: 1px solid $color-lightgray;
          background: #fbfbfb;
          height: 90px;
          margin: 0 -15px 0 -30px;
          padding: 0 15px 0 30px;
          border-radius: 0 0 10px 0;
          img {
            width: auto !important; } }
        &__table {
          display: table;
          width: 100%;
          &>div {
            display: table-cell;
            vertical-align: middle;
            height: 90px;
            &:last-child, &:first-child {
              width: 1px; }
            &+div {
              padding-left: 25px; } } }
        .ion-ios-location {
          color: #9aa1ba;
          margin-right: 10px; }
        p {
          &:last-child {
            margin-bottom: 0; } } }
      &__title {
        font-size: rem(12);
        text-transform: uppercase;
        color: $color-orange;
        line-height: 1.2;
        margin-bottom: 10px; }
      &__price {
        font-size: rem(22);
        text-transform: uppercase;
        color: $color-orange;
        font-family: $font-roboto-bold;
        line-height: 1.2;
        margin-bottom: 3px;
        .fa-rouble {
          font-size: rem(19);
          vertical-align: 1px; } } } }
  .owl-theme {
    .owl-controls {
      margin-top: 5px; }
    .owl-dots {
      width: 567px;
      position: absolute;
      .owl-dot {
        span {
          width: 66px;
          height: 3px;
          background: #c3c5c8; }
        &.active {
          span {
            background: #ee560e; } } } } } }
