 }  // -webkit-tap-highlight-color: $selection-color

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px;
  // overflow: hidden
 }  // position: relative

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px;
 }  // overflow: hidden

body {
  background: $color-white;
  font-size: rem(13);
  font-family: $font-roboto;
  color: $primary-color;
  // overflow: hidden
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

.wrapper {
  position: relative;
  overflow: hidden; }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 15px 0;
  font-family: $font-roboto-bold; }

.h1, h1 {
  font-size: rem(40);
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 25px;
  font-family: $font-roboto; }

.h2, h2 {
  font-size: rem(24);
  line-height: 1.14;
  margin-bottom: 20px; }

.h3, h3 {
  font-size: rem(20);
  line-height: 1.38; }

.h4, h4 {
  font-size: rem(16);
  line-height: 1.38; }

.h5, h5 {
  font-size: rem(13); }

.h6, h6 {
  font-size: rem(12); }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  line-height: 22px;
  margin: 0 0 20px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 400;
  font-family: $font-roboto-bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a, .link {
  color: $link-color;
  text-decoration: none;
  outline: none;
  transition: color 0.35s, background-color 0.35s, border-color 0.35s;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: underline;
    outline: none; } }

a.link {
  &:hover, &:focus, &:active {
    text-decoration: none; } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px; }

/* text align */
.text {
  &-center, &--center {
    text-align: center; }
  &-left, &--left {
    text-align: left; }
  &-right, &--right {
    text-align: right; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-orange;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-orange;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transition: transform(-50%,-50%);
    &:before, &:after {
      background: $color-orange;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; }

  &-thumbnails {
    display: block;
    max-width: 100%;
    height: auto;
    border: 10px solid $color-lightgray;
    border-radius: 4px;
    margin-bottom: 40px;
    &__title {
      width: 100%;
      text-align: right;
      font-size: rem(11);
      color: #afafaf;
      position: relative;
      top: -30px;
      display: block; } } }

/* section */
.section {
  padding: 55px 0;
  min-width: 320px; }

.content {
  padding: 50px 0;
  &--text {
    font-size: rem(15); }
  &__head {
    text-transform: uppercase;
    color: $color-orange;
    font-family: $font-roboto-medium;
    font-size: rem(26);
    line-height: 1.3;
    margin-bottom: 15px;
    padding-left: 12px;
    position: relative;
    &::before {
      top: 6px;
      height: 20px;
      width: 3px;
      background: #e6e8ea;
      position: absolute;
      left: 0;
      content: ''; } }
  .section {
    min-width: inherit; } }

.visible-xxs {
  display: none !important; }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

/* bg */
.bg {
  &--pink {
    background-color: pink !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--p {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--m {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } } }

/* icons */
.icon {
  display: inline-block;
  &--header-phone1 {
    @include sprite($icon-header-phone-1); }
  &--header-phone2 {
    @include sprite($icon-header-phone-2); } }

/* color */
.color {
  &--black {
    color: $color-black !important; }
  &--white {
    color: $color-white !important; } }

.visible-xxs {
  display: none !important; }

.table {
  margin-bottom: 20px;
  td {
    padding: 2px 10px 2px 0; }
  i {
    color: #d9dce0; } }
