/* header */
.header {
  background-color: $color-white;
  // overflow: hidden
  height: 70px;
  position: relative;
  box-shadow: 0 0 10px rgba($color-black,.35);
  z-index: 100;
  @include clearfix;
  &__btn {
    position: absolute;
    right: 0;
    display: none;
    background: none;
    border: 0;
    color: $color-white;
    font-size: rem(13);
    text-transform: uppercase;
    font-family: $font-roboto-bold;
    padding: 0;
    margin-top: 6px;
    i {
      font-size: rem(16);
      margin-right: 5px;
      vertical-align: -1px; } }
  &__logo {
    float: left;
    margin-top: 6px;
    img {
      max-width: 100%;
      height: auto;
      display: block; } }
  &__quote {
    font-size: rem(13);
    font-family: $font-roboto-medium;
    font-style: italic;
    margin-left: 263px;
    margin-top: 17px;
    padding: 1px 0 1px 18px;
    position: relative;
    width: 190px;
    span {
      position: relative;
      z-index: 1; }
    &::before, &::after {
      content: '';
      position: absolute;
      width: 28px;
      height: 18px; }
    &::before {
      left: 0;
      top: 0;
      background: url('data:image/png;base64,') 0 0 no-repeat; }
    &::after {
      right: 0;
      bottom: 0;
      background: url('data:image/png;base64,') 0 0 no-repeat; } }
  &__nav {
    float: right;
    width: 580px;
    position: relative;
    height: 70px;
    &-background {
      height: 70px;
      position: absolute;
      left: 0;
      top: 0;
      width: 1920px;
      background: linear-gradient(to bottom, #ee560e 35px, #c04631 35px);
      &::before, &::after {
        position: absolute;
        left: 0;
        top: 0;
        content: '';
        border-right: 28px solid transparent;
        border-top: 35px solid $color-white; }
      &::after {
        top: inherit;
        bottom: 0;
        border-top: 0;
        border-bottom: 35px solid $color-white; } }
    &-top {
      position: relative;
      z-index: 5;
      text-align: right;
      color: #fdd2bd;
      font-size: rem(11);
      height: 35px;
      line-height: 33px;
      &>div {
        display: inline-block;
        &+div {
          margin-left: 25px; }
        strong {
          font-size: rem(15);
          color: $color-white; }
        .icon {
          margin-right: 10px;
          vertical-align: -4px; } } }
    &-bottom {
      position: relative;
      z-index: 5;
      &-phone {
        display: none;
        color: $color-white;
        .icon {
          margin-right: 10px;
          vertical-align: -4px; } } } } }
